html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  place-content: center;
  place-items: center;
  background: #090b1a;
  font-size: 15px;
}

h1,
h2,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

.card {
  width: 20.9375rem;
  height: 50.625rem;
  margin: 1rem 0.5rem;
  border-radius: 9px;
  overflow: hidden;
  background: #1b1938;
}
.card .overlay {
  display: flex;
  width: 100%;
  height: 30%;
  background: #aa5cdb;
  position: relative;
}
.card .overlay::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  background-image: url("/assets/image-header-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  mix-blend-mode: multiply;
}
.card .info {
  width: 100%;
  height: 70%;
  text-align: center;
  font-family: "Inter", sans-serif;
  font-weight: 700;
}
.card .info h1 {
  color: white;
  width: fit-content;
  margin: 0 auto;
  margin-top: 2.4rem;
}
.card .info h1 .insights {
  color: #aa5cdb;
}
.card .info p {
  font-family: "Lexend Deca", sans-serif;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.6);
  width: fit-content;
  margin: 0 auto;
  margin-top: 1.2rem;
  line-height: 1.7;
}
.card .info ul {
  list-style: none;
  width: fit-content;
  margin: 0 auto;
  margin-top: 2.8rem;
  line-height: 1;
}
.card .info ul li {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 1.6rem;
  color: white;
}
.card .info ul li:not(:last-child) {
  padding-bottom: 2rem;
}
.card .info ul .below-names {
  font-family: "Lexend Deca", sans-serif;
  font-weight: 400;
  font-size: 0.86rem;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
}